<template>
<div class="container">
  <slider class="slider" ref="slider" :options="options" @slide='slide' @tap='onTap' @init='onInit'>
    <slideritem v-for="(item,index) in someList" :key="index">
      <img class="mobile-image" :src="item.image" alt="">
    </slideritem>
    <div slot="loading">loading...</div>
  </slider>

</div>
</template>
<script>
import { slider, slideritem } from 'vue-concise-slider'
export default {
  el: '#app',

  data () {
    return {
      someList:[
        {
          image: this.shoe.image
        },
        {
          image: this.shoe.front
        },
        {
          image: this.shoe.photo
        },
        {
          image: this.shoe.back
        },
        
        {
          image: this.shoe.side
        },
        {
          image: this.shoe.pic
        }
      ],
      options: {
        pagination: false,
        thresholdDistance: 100, 
        thresholdTime: 300, 
        grabCursor: true, 
        speed: 300,
         
        
      }
    }
  },
  props: ['shoe'],

 
    
  components: {
    slider,
    slideritem
  }
}
</script>
<style scoped>
  .slider {
    max-width: 320px;
    z-index: 0;
  }
  .mobile-image {
    width: 100%;
  }
  
  
</style>